import { Base64 } from 'js-base64';
import { AUTH_URL } from '@/config';
import { LOGIN } from '@/store/types';
import { isObject } from '@/util';

export default {
  name: 'Auth',
  created() {
    if (!this.$route.query.code) {
      this.loading = this.$loading({
        lock: true,
        text: '请提供CODE!',
        spinner: 'el-icon-error',
      });
      return;
    }
    if (this.loading) this.loading.close();
    this.loading = this.$loading({
      lock: true,
      text: '登录中',
    });
    this.fetchToken();
  },
  methods: {
    async fetchToken() {
      try {
        const data = await this.$http.get(AUTH_URL, {
          params: { code: this.$route.query.code },
        });
        const { token } = data.data.data;
        const userinfo = this.parseToken(token);
        if (!userinfo) {
          this.error(data.msg);
          return;
        }
        await this.$store.dispatch(LOGIN, {
          token,
          userinfo,
        });
        this.loading.close();
        this.$router.replace(this.$route.query.state || '/');
      } catch (e) {
        this.error();
      }
    },
    error(text = '登录失败!') {
      this.loading.close();
      this.loading = this.$loading({
        lock: true,
        text,
        spinner: 'el-icon-error',
      });
    },
    parseToken(token) {
      if (!token) return null;
      const jwt = token.split('.');
      if (!jwt[1]) return null;
      const payload = JSON.parse(Base64.decode(jwt[1]));
      if (!isObject(payload)) return null;
      return payload;
    },
  },
  render() {},
};
